
$fontcolor_white: #fff;
$fontcolor_grey: #7a7a7a;
$fontcolor_dark: #272727;
$color-red: #f91e39;
$color-lightred: #ff3950;
$discount-green: #00920f;
$shopee-orange: #ee4d2d;

$border-radius-5: 5px;
$border-radius-10: 10px;

$hero-border-radius: 0% 0% 50% 50% / 88% 88% 12% 12%;
$box-shadow-1: rgba(0, 0, 0, 0.05) 0px 3px 12px;
$box-shadow-2: 0 4px 12px 0 rgba(0, 0, 0, 0.2);



.wrap-pageHero {
    width: 100%;
    height: 320px;
    border-radius: $hero-border-radius;
    margin-bottom: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.wrap-pageHero > h1 {
    color: $fontcolor_white;
    font-size: 40px;
    margin-bottom: 30px;
}

.pageHero-breadcrumbs {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.pageHero-breadcrumbs > a, .pageHero-breadcrumbs > p {
    color: $fontcolor_white;
    font-size: 16px;
    font-weight: normal;
    text-decoration: none;
}

.pageHero-icon-1 {
    font-size: 24px;
    color: $color-red;
    margin: 0 20px;
}


