.wrap-allprod {
    width: 100%;
    height: auto;
    background-color: #fff;
}

.allprod-container {
    width: 80%;
    max-width: 1600px;
    height: auto;
    margin: 0 auto 90px;
}

.allprod-header {
    margin: 0 0 30px;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
}
.allprod-header h2 {
    color: #000;
    font-size: 30px;
    margin-bottom: 20px;
}
.allprod-header p {
    color: #000;
    font-size: 16px;
}


// sort option
.allprod-sort {
    padding: 10px 16px;
    font-size: 14px;
    border: none;
    outline: none;
    border-radius: 5px;
    box-shadow: $box-shadow-2;
    cursor: pointer;
    text-transform: uppercase;

    & > option {
        font-size: 14px !important;
        padding: 8px 16px !important;
        cursor: pointer !important;
    }
}

// sort dropdown
.allprod-header {
    & .allprod-sort-dropdown {
        box-shadow: $box-shadow-2;
        border-radius: 5px;
    }

    & .btn-primary {
        color: #000;
        background-color: #fff;
        width: 220px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 16px;
        font-size: 13px;
        border: none;
        outline: none;
        z-index: 10;
        text-transform: uppercase;
    }

    & .btn-check:focus+.btn-primary, & .btn-primary:focus {
        color: #000;
        background-color: #fff;
        border: none;
        box-shadow: none;
    }
    & .btn-check:active+.btn-primary:focus, & .btn-check:checked+.btn-primary:focus, 
    & .btn-primary.active:focus, & .btn-primary:active:focus, & .show>.btn-primary.dropdown-toggle:focus {
        box-shadow:none;
    }
    & .btn-check:active+.btn-primary, & .btn-check:checked+.btn-primary, 
    & .btn-primary.active, & .btn-primary:active, & .show>.btn-primary.dropdown-toggle {
        background-color: #fff;
        color: #000;
    }

    & .dropdown-item {
        border-radius: 6px;
        padding:  8px;
        // &:hover {
        //     background-color: #f0f0f0;
        // }
    }

    & .dropdown-menu.show {
        width: 220px;
        padding: 8px 8px;
        border-radius: 6px;
        transform: translate(0px, 28px) !important;
        z-index: 1;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
    }
}

.only-reg-prodPrice {
    margin-bottom: 20px;

    color: $color-red;
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.3;
    letter-spacing: normal;
}


