// product category
.prod-category {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    margin: 0 auto 40px;
}

.prod-cate-item {
    height: 180px;
    // background-color:rgb(255, 57, 80, 0.15);
    background-color: #fff2f4;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: 2px solid #fff2f4;
    border-radius: 10px;
    transition: all 0.2s ease-in-out;

    &:hover {
        border: 2px solid $color-red;
    }

    & > h3 {
        color: #000;
        font-size: 20px;
    }
}




// Promo page
.promo-container {
    width: 100%;
}

.promo-container > h2 {
    color: #000;
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 10px;
}

.promo-content-1 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    margin: 0 auto 60px;
}

.promo-item-1 {
    height: auto;
    padding: 20px;
    border-radius: 10px;
    // box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
    display: flex;
    flex-direction: column;

    & > a {
        text-decoration: none;
        & > img {
            width: 100%;
            margin-bottom: 10px;
            cursor: pointer;
        }
    }

    &-desc {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &-desc > div > h3 {
        color: #000;
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 4px;
    }
    &-desc > div > p {
        color: #000;
        font-size: 14px;
        font-weight: normal;
        margin-bottom: 10px;
    }
    &-desc > a > button {
        color: $fontcolor_white;
        background-color: $color-red;
        font-size: 12px;
        float: right;
        text-transform: uppercase;
        padding: 8px 20px;
        border: none;
        border-radius: $border-radius-5;
        transition: all 0.3s ease-in-out;
        &:hover {
            background-color: $color-lightred;
        }
    }
}



@media screen and (min-width: 1900px) {

    .prod-cate-item {
        height: 200px;
    }

    .promo-content-1 {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 20px;
    }
}



@media screen and (max-width: 1200px) {
    
    .wrap-pageHero {
        margin-bottom: 40px;
    }

    .allprod-container {
        width: 90%;
    }
    
    .prod-category {
        width: 90%;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 20px;
        margin: 0 auto 20px;
    }

    .prod-cate-item {
        height: 140px;
    }

    .promo-item-1-desc > div > h3 {
        font-size: 16px;
        margin-bottom: 10px;
    }

    .promo-item-1-desc > div > p {
        font-size: 13px;
    }

    
    .prod-cate-item {
        &:hover {
            border: 2px solid #fff2f4;
        }
        &:focus {
            border: 2px solid $color-red;
        }
    }
}


@media screen and (max-width: 1024px) {
    .prod-category {
        width: 90%;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        margin: 0 auto;
    }

    .prod-cate-item {
        height: 160px;
    }

    .promo-content-1 {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        margin: 0 auto 60px;
    }

    .promo-item-1-desc > div > p {
        font-size: 13px;
    }
}


@media screen and (max-width: 576px) {
    
    .wrap-pageHero {
        margin-bottom: 30px;
    }
    .wrap-pageHero > h1 {
        font-size: 32px;
    }
    .pageHero-breadcrumbs > a, .pageHero-breadcrumbs > p {
        font-size: 14px;
        font-weight: bold;
    }

    .allprod-container {
        width: 100%;
        margin: 0 auto 60px;
    }
    
    .allprod-header {
        width: 90%;
        margin: 0 auto 10px;
    }
    
    .allprod-header > h2,
    .promo-container > h2 {
        font-size: 24px;
    }
    .allprod-header > p {
        font-size: 14px;
    }

    .pageHero-icon-1 {
        margin: 0 10px;
    }

    .allprod-container > .prod-content {
        width: 97%;
    }

    // related products
    .proddetails-cont-3-item {
        width: 80%;
        margin: 0 auto;
    }

    .prod-cate-item {
        height: 120px;
    }
    .prod-cate-item > h3 {
        color: #000;
        font-size: 14px;
    }



    // promo page
    .promo-content-1 {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px 10px;
        margin: 0 auto 60px;
    }

    .promo-item-1 {
        padding: 10px;
        border-radius: 4px;
        &-desc > div > h3 {
            color: #000;
            font-size: 14px;
            font-weight: bold;
            margin-bottom: 4px;
        }
        &-desc > div > p {
            font-size: 12px;
            font-weight: normal;
        }
        &-desc > a > button {
            font-size: 10px;
        }
    }

}

